                window.location.href = "#ayat"+ayat;
            }else{
                window.location.href=`/#/app/islamic/baca?nama_surat=${namasurah}&surat=${surah}&ayat=${ayat}`;
            }
        },
        getQuote(){
            axios.get('https://api.banghasan.com/quran/format/json/acak').then(res=>{
